@keyframes scaleUpAndDown {
    0% {
        transform: scale(0);
    }

    50% {
        transform: scale(1.2);
    }

    100% {
        transform: scale(1);
    }
}

@keyframes lightWave {
    0% {
        box-shadow: 0 0 15px 5px #7670ED;
    }

    35% {
        box-shadow: 0 0 15px 5px #57BDFD;
    }

    65% {
        box-shadow: 0 0 15px 5px #E7A9FF;
    }
    100% {
        box-shadow: 0 0 15px 5px #7670ED;
    }
    65% {
        box-shadow: 0 0 15px 5px #E7A9FF;
    }
    35% {
        box-shadow: 0 0 15px 5px #57BDFD;
    }
    0% {
        box-shadow: 0 0 15px 5px #7670ED;
    }
}

@keyframes preloader-rotate {
    100% {
      transform: rotate(360deg);
    }
  }
  
  @keyframes preloader-bounce {
  
    0%,
    100% {
      transform: scale(0);
    }
  
    50% {
      transform: scale(1);
    }
  }
  
  .loaded_hiding .preloader {
    transition: 0.3s opacity;
    opacity: 0;
  }
  
  .loaded .preloader {
    display: none;
  }